/**
 * WordPress dependencies
 */
import {addFilter} from "@wordpress/hooks";

/**
 * Plover dependencies
 */
import {getCustomBlockSupportConfig, getPloverSupport, hasPloverSupport} from "@plover/utils";

/**
 * Internal dependencies
 */
import EventHandlerPanel from "./EventHandlerPanel";

/**
 * Stylesheet
 */
import "./style.scss";

const isSupportEventHandler = (name) => {
    return hasPloverSupport(name, 'ploverEventHandler');
}

addFilter(
    'blocks.registerBlockType',
    'plover/event-handler-attributes',
    (settings, name) => {

        const blockEvents = getPloverSupport(name, 'ploverEventHandler');
        if (!blockEvents) {
            return settings;
        }

        let eventHandlerAttributes = {};
        const allowedEvents = getCustomBlockSupportConfig('eventHandler', {})?.allowedEvents ?? {};
        Object.keys(allowedEvents).forEach((ev) => {
            if (blockEvents[ev]) {
                eventHandlerAttributes[ev] = {
                    type: 'string'
                };
            }
        });

        settings.attributes = {
            ...settings.attributes,
            ...eventHandlerAttributes,
        };

        return settings;
    }
);

addFilter(
    'editor.BlockEdit',
    'plover/event-handler-controls',
    (BlockEdit) => {
        return (props) => {
            const {isSelected, name} = props;
            if (!isSupportEventHandler(name)) {
                return <BlockEdit {...props}/>
            }

            return (
                <>
                    <BlockEdit {...props}/>
                    {isSelected && (
                        <EventHandlerPanel
                            {...props}
                            support={{
                                ...getPloverSupport(props.name, 'ploverEventHandler')
                            }}
                        />
                    )}
                </>
            );
        }
    }
);
