/**
 * WordPress dependencies
 */
import { useState } from '@wordpress/element';
import {
  __experimentalToolsPanel as ToolsPanel,
  __experimentalToolsPanelItem as ToolsPanelItem,
  Button,
  Modal,
} from '@wordpress/components';
import { InspectorAdvancedControls } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';

/**
 * Plover dependencies
 */
import { getCustomBlockSupportConfig } from '@plover/utils';
import { CodeEditor, Tips } from '@plover/components';

function CodeEditorModal ({ label, ...props }) {

  const [isEditorOpen, setEditorOpen] = useState(false);
  const openEditor = () => setEditorOpen(true);
  const closeEditor = () => setEditorOpen(false);

  return (
    <>
      {isEditorOpen && (
        <Modal title={label} onRequestClose={closeEditor} className="plover-code-editor-modal">
          <CodeEditor
            {...props}
            codeMirrorProps={{
              placeholder: __('Code gose here...', 'plover')
            }}
          />
        </Modal>
      )}

      <Button variant="secondary" onClick={openEditor}>
        {label}
      </Button>
    </>
  );
}

export default function EventHandlerPanel ({ support, attributes, setAttributes }) {
  const { defaultControls, ...blockEvents } = support;
  const allowedEvents = getCustomBlockSupportConfig('eventHandler', {})?.allowedEvents ?? {};

  const validEvents = Object.keys(allowedEvents)
    .filter(e => !!blockEvents[e])
    .map((e) => {
      return { event: e, ...allowedEvents[e] };
    });

  if (!validEvents || validEvents.length <= 0) {
    return null;
  }

  const [eventsPanel, setEventsPanel] = useState({});
  const deselectEventPanel = (event) => {
    setEventsPanel({ ...eventsPanel, [event]: undefined });
  };

  const resetAll = () => {
    setEventsPanel({});
  };

  return (
    <InspectorAdvancedControls>
      <ToolsPanel
        resetAll={resetAll}
        label={__('JavaScript Event Handler', 'plover')}
        className="plover-event-handler-settings-panel"
      >
        <Tips style={{ gridColumn: 'span 2' }}>
          <p>
            {__('Add custom JavaScript snippet to the event handlers in this block.', 'plover')}
          </p>
          <a href="https://wpplover.com/docs/plover-kit/modules/block-event-handler/" target="_blank">
            {__('Documentation â†—', 'plover')}
          </a>
        </Tips>

        {validEvents.map(({ event, label }) => (
          <ToolsPanelItem
            key={event}
            hasValue={() => !!eventsPanel[event]}
            label={label}
            onDeselect={() => deselectEventPanel(event)}
            isShownByDefault={defaultControls[event] || attributes[event]}
          >
            <CodeEditorModal
              label={__('Edit', 'plover') + ' ' + label}
              value={attributes[event] ?? ''}
              onChange={(v) => {
                setAttributes({ [event]: v });
              }}
            />
          </ToolsPanelItem>
        ))}
      </ToolsPanel>
    </InspectorAdvancedControls>
  );
}